@media only screen and (min-width: 769px) and (min-aspect-ratio: 1/1) {
	html {
		font-size: .9vw;
	}
}


@media only screen and (min-width: 769px) and (min-aspect-ratio: 104/71) {
	html {
		font-size: .7vw;
	}
}

@media only screen and (min-width: 769px) and (min-aspect-ratio: 11/5) {
	html {
		font-size: 1.65vh;
	}

	.content {
		height: 100%;
	}
}

@media only screen and (max-width: 768px) and (orientation: portrait) {
	.hidden-mobile-portrait {
		display: none;
	}

	.main-nav .dropdown {
		display: block;
	}
}

@media only screen and (max-width: 768px) and (orientation: landscape) {
	html {
		font-size: .79vw;
	}

	h2 {
		font-size: 4.5rem;
		line-height: 1.1;
	}

	.section-title {
		margin-top: 2rem;
		margin-left: 2rem;
		max-width: 33rem;
	}

	.info-screen {
		.section-title {
			max-width: 26rem;
		}
	}

	.content {
		height: 100%;
		max-height: 84rem;

		.dscr {
			width: 51rem;
			min-height: 51rem;
			left: 52rem;
			font-size: 2.4rem;
			padding: 3rem;

			.main-p {
				line-height: 1.25em;
				// height: 48rem;
			}

			.note {
				// display: none;
				font-size: 1.7rem;
				line-height: 1.25em;
			}

		}
	}

	.main-screen .logo {
		height: 20rem;
		width: 66rem;

		h1 {
			font-size: 7rem;
		}

		img {
			height: 8rem;
			margin-right: 4rem;
		}
	}

	.mediakit-lite .logo h1 {
		font-size: 5rem;
	}

	.main-nav {
		ul {
			max-width: 116rem;
		}

		a {
			font-size: 1.8rem;
			white-space: nowrap;
		}
	}


	// stats

	.stats-slider {
		.tabs {
			top: 23rem;
		}

		.tabs-wrapper {
			left: 0;
			top: 23rem;
			padding-left: 12rem;
		}

		.tab-content {
			.big-number {
				font-size: 8rem;
			}

			p {
				font-size: 2.6rem;
			}

			.progress-line {
				height: 1.6rem;
			}

			.progress-number {
				font-size: 2.2rem;
			}
		}
	}

	// promo
	.promo {
		.tabs {
			margin-top: 2rem;
			margin-right: -8rem;
			padding: 2rem 0;
		}

		ul {
			font-size: 2rem;
		}
	}

	.tab-contents {
		flex-wrap: wrap;
		// top: 28rem;

		.tab-image {
			flex-basis: 42rem;
		}

		.tab-dscr {
			flex-basis: 53rem;
		}

		h4 {
			font-size: 3.4rem;
		}

		p {
			font-size: 2.2rem;
			margin-bottom: 2rem;
			max-height: 7em;
			overflow: hidden;
		}

		a {
			font-size: 2rem;
		}
	}


	.partners-list ul {
		margin-top: 23rem;
	}

}





@media only screen and (max-width: 1024px) and (orientation: portrait) {
	html {
		font-size: 1.8vw;
	}

	.main-nav {
		a {
			font-size: 1.8rem;
		}
	}

	.corners {
		display: none;
	}

	.content {
		padding: 3rem;
		height: 100%;
		max-width: 100%;
		max-height: 84rem;

		.dscr {
			left: initial;
			top: initial;

			font-size: 2.2rem;

			right: 5rem;

			bottom: 3rem;
			height: 53rem;
			width: 42rem;
			padding: 2.5rem;

			.main-p {
				line-height: 1.2em;
				// height: 35rem;
				min-height: 0;
				overflow-y: auto;
				overflow-x: hidden;
			}

			.note {
				font-size: 1.7rem;
				line-height: 1.25em;
			}
		}
	}

	.section-title {
		padding-left: 5rem;
		margin-left: 0;
		margin-top: 3rem;

		&:before {
			left: 1.5rem;
		}
	}



	// stats
	.stripe-bg {
		width: 20rem;
	}
	.stats-slider {
		.tabs-wrapper {
			top: 45rem;
			width: 40rem;
			left: 4rem;
			padding-left: 4rem;
		}

		.tabs {
			top: 18rem;
			left: 4rem;
		}
	}

	// promo
	.promo {
		.content {
			display:flex;
			flex-direction: column;
		}

		.section-title {
			width: 100%;
			br {
				display: none;
			}
		}

		.head {
			flex-wrap: wrap;
			margin-bottom: 2rem;
			flex: 0 0 auto;
		}

		.tabs {
			margin-top: 2rem;

			ul {
				font-size: 2rem;
			}

			li {
				margin: .7rem 1rem;
			}
		}

		.tab-contents {
			position: relative;
			flex: 1 1 35rem;
			top: 0;
			left: 0;
			margin-top: 2rem;

			.tab-image {
				display: none;
			}

			.tab-dscr {
				margin: 0;
				flex-basis: 100%;

				.text-wrapper {
					padding: 1.4rem;
					flex: 1 1 26rem;
					margin-bottom: 3rem;
					overflow-y: auto;
					overflow-x: hidden;

					p {
					}
				}
			}

			h4 {
				font-size: 3rem;
			}
			p {
				font-size: 2.2rem;
			}
			a {
				padding: 1.4rem;
				font-size: 2rem;
			}
		}
	}


	// partenrs
	.partners {
		.content {
			display: flex;
			flex-direction: column;
		}

		.section-title {
			flex: 0 0 auto;
		}
	}
	.partners-list {
		flex: 1 1 auto;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 2rem 0;

		ul {
			margin:auto;
			padding: 0;
			justify-content: center;
			align-items: center;
		}
	}
}

@media only screen and (max-width: 768px) and (orientation: portrait) and (min-aspect-ratio: 65/100) {
	html {
		font-size: 1.6vw;
	}
}


@media only screen and (max-width: 768px) and (orientation: portrait) and (min-aspect-ratio: 80/100) {
	html {
		font-size: 1.3vw;
	}
}

@media only screen and (max-width: 1024px) and (orientation: portrait) and (min-aspect-ratio: 60/100) {
	html {
		font-size: 1.35vw;
	}
}