.promo {
	background-color: $text-color;
	color: #fff;

	h2 {
		color: $yellow;
	}

	&-inn {
		position: relative;
		width: 100%;
		height: 100%;
	}

	.head {
		display: flex;

		align-items: flex-start;
	}

	.section-title {
		background-color: transparent;

		margin-right: 2rem;
		max-width: none;

		&:before {
			background-color: $yellow;
		}

		&.special{
			&:before {
				background-color: transparent;
			}
		}
	}

	&:before {
		content: '';
		display: block;
		position: absolute;
		height: 32px;
		width: 100%;
		bottom: 0;
		left: 0;
		background: repeating-linear-gradient(135deg, #222, #222 6px, transparent 6px, transparent 12px);
	}

	&.events {
		.tabs {
			max-width: 48rem;
		}
	}
}

.mediakit-lite  {
	.tabs {
		margin-top: 0;
	}

	.tab-contents {
		top: 26rem;
	}
}
.tabs {
	// float: left;
	// max-width: 65rem;
	margin-top: 7rem;


	ul {
		list-style: none;
		margin: 0;
		padding: 0;

		font-size: 1.6rem;
		will-change: opacity;
	}

	li {

		display: inline-block;
		margin: .5rem 2rem;
		cursor: pointer;
		opacity: .8;

		transition: opacity .2s;

		&:hover {
			opacity: 1;
		}

		&.vip {
			color: $mud;
			position: relative;

			&:before {
				content: '';
				display: block;
				position: absolute;
				height: 1.25rem;
				width: 1.25rem;
				background-image: url(../img/star.svg);
				background-size: contain;
				background-position: center center;
				left: -1.6rem;
				top: .14rem;
				filter: grayscale(50%) brightness(70%);
			}
		}

		&.active {
			color: $yellow;
			opacity: 1;

			&.vip:before {
				filter: none;
			}
		}
	}
}


.tab-contents {
	position: absolute;
	top: 30rem;
	bottom: 0;
	left: 0;
	right: 0;

	.tab-content {
		position: absolute;
		display: flex;
		justify-content: center;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		opacity: 0;
		pointer-events: none;
		transition: opacity .6s, filter .6s;
		filter: blur(1px);
		will-change: opacity;

		&.active {
			opacity: 1;
			transition: opacity .6s, filter .6s;
			filter: blur(0px);
			pointer-events: initial;
		}
	}

	.tab-image {
		display: flex;
		justify-content: flex-end;
		align-items: flex-end;
		flex: 0 0 50rem;
		// width: 45rem;
		height: 100%;
		margin-right: 4rem;
		box-sizing: border-box;

		img {
			max-height: 100%;
			max-width: 100%;
			width: auto;
		}
	}

	.tab-dscr {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex: 0 0 37rem;
		margin-left: 4rem;
		// width: 33rem;
		padding-bottom: 7rem;
	}


	h4 {
		font-size: 2.4rem;
		margin-top: 0;
		margin-bottom: 2rem;
	}

	p {
		font-size: 1.6rem;
	}

	a,
	.site-link {
		font-size: 1.4rem;
	}

	a {
		color: $mud;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

}