
.main-screen {
	.main-screen-bg {
		position: absolute;
		height: 100%;
		width: 100%;
		overflow: hidden;
		// background: red;

		.stripe-container {
			position: absolute;
			height: 200rem;
			width: 261%;
			left: -10%;
			// top: 50%;
			transform: rotateZ(45deg);
			transform-origin: 50% 0;

			transition: height 1.8s linear;



			div {
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;

				&:nth-child(1) {
					top: -10%;
					background: repeating-linear-gradient(90deg, #292929 .4rem, #292929 1.9rem, transparent 1.9rem, transparent 15.4rem);
				}

				&:nth-child(2) {
					top: -20%;
					background: repeating-linear-gradient(90deg, #292929 3.4rem, #292929 4.9rem, transparent 4.9rem, transparent 18.4rem);
				}

				&:nth-child(3) {
					top: -30%;
					background: repeating-linear-gradient(90deg, #292929 6.4rem, #292929 7.9rem, transparent 7.9rem, transparent 21.4rem);
				}

				&:nth-child(4) {
					top: -25%;
					background: repeating-linear-gradient(90deg, #292929 9.4rem, #292929 10.9rem, transparent 10.9rem, transparent 24.4rem);
				}

				&:nth-child(5) {
					top: -20%;
					background: repeating-linear-gradient(90deg, #292929 12.4rem, #292929 13.9rem, transparent 13.9rem, transparent 27.4rem);
				}
			}

		}

	}

	&-inn {
		position: relative;
		// height: 58.8rem;
		height: calc(100% - 13.2rem);
		width: calc(100% - 13.2rem);
		max-width: 112rem;
		// max-height: 57.8rem;

		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		// background: repeating-linear-gradient(-45deg, #000 .4rem, #000 1.9rem, transparent 1.9rem, transparent 3.4rem);
	}


	.logo {
		position: relative;
		height: 18.6rem;
		width: 41rem;
		top: 50%;
		left: -1rem;
		padding-left: 1rem;
		transform: translateY(-50%);
		background-color: #fff;
		vertical-align: middle;

		&:before {
			content: '';
			display: inline-block;
			height: 100%;
			width: 0;
		}

		&:before,
		img,
		h1 {
			vertical-align: middle;
		}

		img {
			display: inline-block;
			height: 5rem;
			width: auto;
			margin-right: 2rem;
		}

		h1 {
			font-family: 'SF-UI', sans-serif;
			font-size: 4rem;
			display: inline-block;
		}
	}

	.mouse-down {
		position: absolute;
		bottom: -5.5rem;
		left: 50%;
		transform: translateX(-50%);
		opacity: 0;
		animation: mouse-down 5s;

		img {
			max-width: 4.6rem;
		}
	}
}

@keyframes mouse-down {
	0% {
		opacity: 0;
	}

	10% {
		opacity: .8;
	}

	90% {
		opacity: .8;
	}

	100% {
		opacity: 0;
	}
}

.wrapper.preload {
	.main-screen {

		.stripe-container {
			height: 0;
		}

		.logo {
			img {
				animation: logo-preload 1.6s infinite linear;
			}
		}

		.mouse-down {
			opacity: 0;
			animation: none;
		}
	}
}

@keyframes logo-preload {
	0% {
		opacity: 1;
	}

	50% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

.mediakit-lite .logo {
	width: 46rem;
	h1 {
		line-height: 1;
		font-size: 3.6rem;
	}
}
