.partners {
	background-color: #fff;

	.partners-inn {
		position: relative;
		height: 100%;
		width :100%;
	}
}

.partners-list {

	height: 100%;
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 0;
	// overflow: hidden;

	.partners-bg {
		background-color: red;
		display: block;
		position: absolute;
		top: 24rem;
		bottom: 0;
		width: 200vw;
		left: 50%;
		background: url(../img/partners.jpg) 0 0 repeat-x;
		background-size: auto 100%;

		animation: partners-bg linear 70s infinite;
		// transform: tranlateZ(0);
		will-change: transform;
	}



	ul {
		display: flex;
		flex-wrap: wrap;
		max-width: 70rem;
		margin: auto;
		margin-top: 26rem;

		list-style: none;
	}

	li {
		display: block;
		height: 10rem;
		width: 10rem;
		margin: 2rem;

		background: #f1f1f2 no-repeat center center;
		background-size: contain;

	}

	a {
		display: block;
		height: 100%;
		width: 100%;
	}
}


@keyframes partners-bg {
	0% {
		transform: translateX(-25%);

	}

	100% {
		transform: translateX(-50%);
	}

}