html,
body,
.wrapper {
	height: 100%;
	width: 100%;
}

html,
body {
	overflow: hidden;
}

html {
	font-size: 10px;
}

body {
	font-family: 'Fira Sans', sans-serif;
	color: $text-color;
}

.wrapper {
	position: relative;
	z-index: 1;
}

section {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	transform: translateY(100%);

	&.main-screen {
		transform: translateY(0);
	}
}

h2 {
	font-family: 'SF-UI', sans-serif;
	font-size: 3.8rem;
	margin: 0;
	padding: 0;
}

p {
	font-size: 1em;
	margin: 0;
}

.mediakit-lite {
	.section-title {
		display: none;
	}
}

.color-brand {
	color: $mud;
}

.section-title {
	position: relative;
	float: left;
	padding: 2rem 2rem 2rem 4rem;
	background-color: #fff;
	max-width: 26rem;
	margin-top: 5rem;
	margin-left: 5rem;


	&:before {
		content: '';
		display: block;
		position: absolute;
		height: 2rem;
		width: 2rem;
		left: 0;
		top: 50%;
		margin-top: -1rem;

		border-radius: 50%;
		background-color: $mud;
	}

	&.special {
		&:before {
			background: url(../img/star.svg);
			background-color: transparent;
		}
	}
}

.content {

	.dscr {
		position: relative;
		font-size: 1.8rem;
		position: absolute;
		width: 41rem;
		min-height: 44.5rem;
		left: 55%;
		bottom: 0;
		padding: 4rem;
		background-color: #fff;
		box-sizing: border-box;


		.main-p {
			height: 100%;
			// margin-bottom: 2rem;
			overflow-y: auto;
			overflow-x: hidden;
		}

		.note {
			font-size: 1rem;
			color: $mud;
			line-height: 1;
		}


		& > * {
			position: relative;
			z-index: 1;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			width: calc(100% + 3.2rem);
			height: calc(100% + 3.2rem);
			left: -1.6rem;
			top: -1.6rem;
			background: repeating-linear-gradient(135deg, #fff .5rem, #fff 1rem, transparent 1rem, transparent 1.5rem);
			z-index: 0;
		}

		.bigger {
			font-size: 2.4rem;
			font-weight: 500;
		}
	}
}