.main-header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;

	z-index: 99;

	&.secondary:hover {
		.main-nav {
			transform: translateY(0);
		}
	}

	.main-nav {
		transition: transform .4s;
	}
}

.wrapper.preload .main-header {
	.main-nav {
		transform: translateY(-100%) !important;
	}
}

.main-header.secondary {
	.main-nav {
		transform: translateY(-100%);
	}

	transition: opacity .1s;

	&.hidden {
		opacity: 0;
		pointer-events: none;
	}
}



.main-nav {

	// background: linear-gradient(to bottom, #fff, transparent);
	background-color: #fff;
	font-family: 'SF-UI', sans-serif;

	ul {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		max-width: 108rem;
		height: 6.6rem;
		margin: 0 auto;


		padding: 0;
		list-style: none;
	}

	li {
		color: #999;
		padding: 0 .8rem;
		transition: color .3s;
		font-weight: 400;

		&:hover {
			color: $text-color;
		}

		&.active {
			color: $mud;
		}
	}

	a {
		color: inherit;
		text-decoration: none;
		padding: .5rem;
		font-size: 1.3rem;
	}

	.dropdown {
		position: relative;
		margin-left: 2rem;
		display: none;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 100%;
			top: .5rem;
			margin-left: -.6rem;
			border-top: .5rem solid;
			border-left: .5rem solid transparent;
			border-right: .5rem solid transparent;

			transform-origin: center center;
			transition: transform .2s;
		}

		&:hover {
			&:before {
				// transform: scaleY(-1);
			}

			.sublist {
				transform: scaleY(1);
				opacity: 1;
			}
		}
	}


	.sublist {
		position: absolute;
		top: 100%;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		height: auto;
		padding-top: 2rem;
		transform-origin: center top;
		transform: scaleY(0);
		opacity: 0;

		transition: transform .2s, opacity .2s;

		li {
			padding: .5rem 0;
			text-align: left;
		}

		a {
			white-space: nowrap;
		}
	}
}
