.stats {
	overflow: hidden;
}

.stats-screen-inn {
	position: relative;
	height: 100%;
	width: 100%;
	overflow: hidden;
	background-color: #fff;
}

.stripe-bg {
	position: absolute;
	z-index: 1;
	top: 0;
	right: 0;
	height: 200%;
	width: 30rem;
	background: repeating-linear-gradient(135deg, #f8f8f8 0, #f8f8f8 5rem, transparent 5rem, transparent 10rem);

	&.activate-prev {
		animation: stripe-next .8s 1;
	}

	&.activate-next {
		animation: stripe-prev .8s 1;
	}
}

@keyframes stripe-prev {
	0% {
		transform: translateY(0);
	}

	100% {
		transform: translateY(-42rem);
	}
}
@keyframes stripe-next {
	0% {
		transform: translateY(-42rem);
	}

	100% {
		transform: translateY(0);
	}
}


.stats-slider {
	height: 100%;
	top: 0%;
	left: 0;

	.tabs {
		position: absolute;
		left: 55%;
		top: 26rem;
		margin: 0;

		ul {
			font-family: 'SF-UI', sans-serif;
			font-weight: 600;
			list-style: none;
			margin: 0;
			padding: 0 0 0 4rem;

			font-size: 3rem;
		}

		li {
			display: block;
			margin: 0 auto 1.6rem;

			&.active {
				color: $mud;
			}

		}
	}

	.tabs-wrapper {
		position: absolute;
		width: 26rem;
		top: 26rem;
		left: 18%;
		padding-left: 4rem;
		bottom: 0;
	}

	.tab-content {
		position: absolute;
		width: 100%;
		top: 0;

		transform: translateY(0);
		opacity: 0;
		pointer-events: none;

		&.active {
			opacity: 1;
			pointer-events: initial;
		}

		&-1 {
			li {
				margin: 0 auto 3rem;
			}
		}

		ul {
			list-style: none;
			margin: 0;
			padding: 0;
		}

		p {
			font-size: 2rem;
			font-weight: 400;
		}

		.big-number {
			color: $mud;
			font-size: 6rem;
			font-weight: 600;
			white-space: nowrap;
			line-height: 1em;

			@supports (-webkit-background-clip: text) {
				color: transparent;
				background: repeating-linear-gradient(135deg, $mud 0px, $mud .15rem, transparent .15rem, transparent .3rem);
				-webkit-background-clip: text;
			}

			@supports (background-clip: text) {
				color: transparent;
				background: repeating-linear-gradient(135deg, $mud 0px, $mud .15rem, transparent .15rem, transparent .3rem);
				-webkit-background-clip: text;
			}
		}

		.progress {
			white-space: nowrap;
			margin-bottom: 2.5rem;
			p {
				margin-bottom: .5rem;
			}
		}


		.progress-number,
		.progress-line {
			vertical-align: middle;
		}

		.progress-line {
			display: inline-block;
			background: repeating-linear-gradient(135deg, $mud 0, $mud 3px, transparent 3px, transparent 6px);
			height: 1.2rem;
			margin-right: 1rem;
		}

		.progress-number {
			font-size: 1.8rem;
			color: $mud;
		}
	}
}