
.corners {
	position: absolute;
	height: 100%;
	width: 100%;
	overflow: hidden;
	// z-index: 1;

	&.show {
		// transform: translateY(0);
	}
}

.corner-left,
.corner-right {
	display: block;
	position: absolute;
	transform: translate(-50%, -50%) rotateZ(45deg);
	transform-origin: 50% 50%;
	background-color: #fff;

	&:before {
		content: '';
		display: block;
		padding-top: 100%;
	}
}

.corner-left {
	width: calc(.40 * 141.42%);
	top: 0;
	left: 0;
}

.corner-right {
	width: calc(.60 * 141.42%);
	top: 100%;
	left: 100%;
}