.info-screen {
	background-color: #fff;

	.section-bg {
		position: absolute;
		z-index: 0;
		height: 100%;
		width :100%;
		transition: transform .8s;
		// overflow: hidden;

		div {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background: #fff no-repeat center;
			background-size: cover;
			transform: translateY(100%);

			&.active {
				transform: translate(0);
			}

			&:first-child {
				background-image: url(../img/about-bg-1.jpg);
			}

			&:nth-child(2) {
				background-image:  url(../img/about-bg-2.jpg);
			}

			&:nth-child(3) {
				background-image:  url(../img/about-bg-3.jpg);
			}
		}

	}

	&.readers .section-bg div {
		background-image: url(../img/about-bg-4.jpg);
	}

	.layouts-wrapper {
		position: absolute;
		left: 0;
		right: 0;
		top:0;
		bottom: 0;
		// height: 100px;
		padding:inherit;
		overflow: hidden;
	}

	.layout {
		position: absolute;
		top: 0;
		left: 0;
		padding: inherit;
		opacity: 0;
		pointer-events: none;
		height: 100%;
		box-sizing: border-box;	

		&.active {
			opacity: 1;
			transform: translateY(0);
			pointer-events: initial;
		}

		&.activate-next {
			animation: about-la-activate-next .8s 1 .2s;
		}

		&.activate-prev {

			animation: about-la-activate-prev .8s 1 .2s;
		}

		&.deactivate-next {
			animation: about-la-deactivate-next .6s 1;
		}

		&.deactivate-prev {
			animation: about-la-deactivate-prev .8s 1;
		}
	}
}



.info-screen-inn {
	position: relative;
	height: 100%;
	width: 100%;
	overflow: hidden;
}


@keyframes about-la-activate-next {
	0% {
		opacity: 0;
		transform: translateY(4rem);
	}
	100% {
		opacity: 1;
		transform: translateY(0rem);
	}
}

@keyframes about-la-activate-prev {
	0% {
		opacity: 0;
		transform: translateY(-4rem);
	}
	100% {
		opacity: 1;
		transform: translateY(0rem);
	}
}

@keyframes about-la-deactivate-next {
	0% {
		opacity: 1;
		transform: translateY(0rem);
	}
	100% {
		opacity: 0;
		transform: translateY(-4rem);
	}
}


@keyframes about-la-deactivate-prev {
	0% {
		opacity: 1;
		transform: translateY(0rem);
	}

	100% {
		opacity: 0;
		transform: translateY(4rem);
	}
}