section {


		&.active {
			transform: translateY(0);
		}

		&.activate-next {
			// transform: translateY(0);
			animation: about-main-activate-next .8s 1;

			.section-title,
			.section-title + .tabs  {
				animation: about-main-activate-next .8s 1;
			}

		}

		&.activate-prev {
			// transform: translateY(0);
			animation: about-main-activate-prev .8s 1;

			.section-title,
			.section-title + .tabs {
				animation: about-main-activate-prev 0.8s 1;
			}
		}

		&.deactivate-next {
			animation: about-main-deactivate-next .8s 1; 

			.section-title,
			.section-title + .tabs  {
				animation: about-main-deactivate-next .8s 1;
			}
		}

		&.deactivate-prev {
			animation: about-main-deactivate-prev .8s 1;

			.section-title,
			.section-title + .tabs  {
				animation: about-main-deactivate-prev .8s 1;
			}
		}

}

.section-bg {
	div {
		&.activate-next {
			animation: about-main-activate-next .8s 1;
		}

		&.activate-prev {
			animation: about-main-activate-prev .8s 1;
		}

		&.deactivate-next {
			animation: about-main-deactivate-next .8s 1; 
		}

		&.deactivate-prev {
			animation: about-main-deactivate-prev .8s 1;
		}
	}
}

.stats-slider {
	.tabs li {
			&.activate-next {
				// transform: translateY(0);
				animation: tab-activate-next .8s 1;
			}

			&.activate-prev {
				// transform: translateY(0);
				animation: tab-activate-prev .8s 1;
			}

			&.deactivate-next {
				animation: tab-deactivate-next .8s 1;
			}

			&.deactivate-prev {
				animation: tab-deactivate-prev .8s 1;
			}
	}

	.tab-content {
			&.activate-next {
				// transform: translateY(0);
				animation: tab-content-activate-next .8s 1;
			}

			&.activate-prev {
				// transform: translateY(0);
				animation: tab-content-activate-prev .8s 1;
			}

			&.deactivate-next {
				animation: tab-content-deactivate-next .8s 1;
			}

			&.deactivate-prev {
				animation: tab-content-deactivate-prev .8s 1;
			}
	}
}

@keyframes about-main-activate-next {
	0% {
		transform: translateY(100%);
	}
	100% {
		transform: translateY(0);
	}
}

@keyframes about-main-activate-prev {
	0% {
		transform: translateY(-100%);
	}
	100% {
		transform: translateY(0);
	}
}

@keyframes about-main-deactivate-next {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(-100%);
	}
}


@keyframes about-main-deactivate-prev {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(100%);
	}
}



// ############################



@keyframes tab-activate-next {
	0% {
		color: $text-color;
	}

	100% {
		color: $mud;
	}
}

@keyframes tab-activate-prev {
	0% {
		color: $text-color;
	}

	100% {
		color: $mud;
	}
}

@keyframes tab-deactivate-next {
	0% {
		color: $mud;
	}

	100% {
		color: $text-color;
	}
}


@keyframes tab-deactivate-prev {
	0% {
		color: $mud;
	}

	100% {
		color: $text-color;
	}
}


@keyframes tab-content-activate-next {
	0% {
		opacity: 0;
		transform: translateY(4rem);
	}
	100% {
		opacity: 1;
		transform: translateY(0rem);
	}
}

@keyframes tab-content-activate-prev {
	0% {
		opacity: 0;
		transform: translateY(-4rem);
	}
	100% {
		opacity: 1;
		transform: translateY(0rem);
	}
}

@keyframes tab-content-deactivate-next {
	0% {
		opacity: 1;
		transform: translateY(0rem);
	}
	100% {
		opacity: 0;
		transform: translateY(-4rem);
	}
}


@keyframes tab-content-deactivate-prev {
	0% {
		opacity: 1;
		transform: translateY(0rem);
	}

	100% {
		opacity: 0;
		transform: translateY(4rem);
	}
}